@import "../../const/variables.scss";
:root {
    --primary-bg: #242526;
    --secondary-bg: #fff;
    --primary-text-color: #555;
    --secondary-text-color: #cecece;
    --border-radius: 8px;
    --speed: 500ms;
  }
  
//   .apadd {
//     background-color: var(--primary-bg);
//   }

  .coffee-img{
    position: relative;
    height: 100px;
    width: 100px;
    overflow: hidden;
    background-color:--primary-text-color;
  }
  
   .add{
    // position: absolute;
    // top: 20px;
    // right: 20px;
    height: 4.5vh;
    width: 7vw;
    border-radius: 10px;
    overflow: hidden;
    cursor: pointer;
    font-size: 1em;
    font-weight:500;
    background-color: #fff;
    transition:  all 0.3s;
                &:hover{
                    transform: scale(1.05);
                }
  
  }



  .welcomeadd{
    width: 100%;
    text-align: center;
    font-size: 18px;
    // padding: 20px 0;
    font-weight: 500;
    color:$accent-variant;
    // line-height: 1.2rem;
  }
  .correoadd{
    text-align: center;
    font-size: 15px;
    margin-bottom: 1px;
    margin-top: 5px;
  }
  .formCorreoadd{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
    margin: 0 auto;
    padding: 20px;
    border-radius: 5px;
    input{
    margin-bottom: 20px ;
    border-color:$accent;
    width: 30vw;
    border: 0;
    height:4.5vh;
    border-radius: 10px ;
    font-size: 1em;
    text-align: center;
    transition: all 0.3s;
    }
  }

  .dropdown-menu-add {
    position: absolute;
    top: 12vh;
    right: 10vw;
    // background-color: #fff;
    border-radius: var(--border-radius);
    padding: 0px 20px;
    width: 80vw;
  }

  
  .dropdown-menu-add.active{
    background-color: var(--primary-text-color);
    opacity: 0.95;
    visibility: visible;
    transform: translateY(0);
    transition: var(--speed) ease;
    z-index: 2;
    text-align: center;
    padding-bottom: 4.5vh;

    a {
    text-decoration: none;  
    color:$accent-variant;     
    }
    button {
      margin-top: 2.5vh;
      height: 4.5vh;
      width: 20vw;
      border-radius: 10px;
      overflow: hidden;
      cursor: pointer;
      font-size: 1em;
      font-weight:500;
      background-color:$accent-variant;
      transition:  all 0.3s;
                  &:hover{
                      transform: scale(1.05);
                  }
    }
  }
  
  .dropdown-menu-add.inactive{
    opacity: 0;
    visibility: hidden;
    transform: translateY(-20px);
    transition: var(--speed) ease;
  }

  @media handheld, only screen and (max-width: 767px) {
    .add{
      // position: absolute;
      // top: 20px;
      // right: 20px;
      height: 3.5vh;
      width: 20vw;
      font-size: 0.75em;
    }

    .dropdown-menu-add {
      top: 12vh;
      right: 5vw;
    }

    .formCorreoadd{

      input{
      width: 50vw;
      }
    }

    .dropdown-menu-add.active{
      button {
        width: 40vw;
      }
    }
}