@import "../../../const/variables.scss";
:root {
    --primary-bg: #242526;
    --secondary-bg: #fff;
    --primary-text-color: #555;
    --secondary-text-color: #cecece;
    --border-radius: 8px;
    --speed: 250ms;
  }
  
//   .apedit {
//     background-color: var(--primary-bg);
//   }

  .coffee-img{
    position: relative;
    height: 100px;
    width: 100px;
    overflow: hidden;
    background-color:--primary-text-color;
  }
  
   .edit{
      background-color: #fff;
      border-radius: 10px;
      cursor: pointer;
      font-size: 1em;
      font-weight: 500;
      height: 4.5vh;
      overflow: hidden;
      transition: all .3s;
      width: 7vw;

    top: 20px;
    right: 20px;
    font-weight:500;
    transition:  all 0.3s;
                &:hover{
                    transform: scale(1.05);
                }
  
  } 

  .welcomeedit{
    width: 100%;
    text-align: center;
    font-size: 18px;
    // padding: 20px 0;
    font-weight: 500;
    color:$accent-variant;
    // line-height: 1.2rem;
  }
  .correoedit{
    text-align: center;
    font-size: 15px;
    margin-bottom: 1px;
    margin-top: 5px;
  }
  .formCorreoedit{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
    margin: 0 auto;
    padding: 20px;
    border-radius: 5px;
    input{
    margin-bottom: 20px ;
    border-color:$accent;
    width: 10vw;
    border: 0;
    height:4.5vh;
    border-radius: 10px ;
    font-size: 1em;
    text-align: center;
    transition: all 0.3s;
    }
  }

  .dropdown-menu-edit {
    position: absolute;
    top: -35vh;
    right: -15vw;
    // background-color: #fff;
    border-radius: var(--border-radius);
    padding: 0px 20px;
    width: 10vw;
  }

  
  .dropdown-menu-edit.active{
    background-color: var(--primary-text-color);
    opacity: 0.95;
    visibility: visible;
    transform: translateY(0);
    transition: var(--speed) ease;
    z-index: 2;
    text-align: center;
    padding-bottom: 4.5vh;

    a {
    text-decoration: none;  
    color:$accent-variant;     
    }
    Link {
      margin-top: 2.5vh;
      height: 4.5vh;
      width: 10vw;
      border-radius: 10px;
      overflow: hidden;
      cursor: pointer;
      font-size: 1em;
      font-weight:500;
      background-color:$accent-variant;
      transition:  all 0.3s;
                  &:hover{
                      transform: scale(1.05);
                  }
    }

    button {
      margin-top: 2.5vh;
      height: 4.5vh;
      width: 10vw;
      border-radius: 10px;
      overflow: hidden;
      cursor: pointer;
      font-size: 1em;
      font-weight:500;
      background-color:$accent-variant;
      transition:  all 0.3s;
                  &:hover{
                      transform: scale(1.05);
                  }
    }


  }
  
  .dropdown-menu-edit.inactive{
    opacity: 0;
    visibility: hidden;
    transform: translateY(-20px);
    transition: var(--speed) ease;
  }

  @media handheld, only screen and (max-width: 767px) {

    .dropdown-menu-edit {
      right: -15vw;
      width: 60vw;
    }
    .formCorreoedit{
      input{
      width: 50vw;
      }
    }

    .dropdown-menu-edit.active{
      button {
        width: 40vw;
      }
    }
  }