@import "../../../../const/variables.scss";
:root {
    --primary-bg: #242526;
    --secondary-bg: #fff;
    --primary-text-color: #555;
    --secondary-text-color: #cecece;
    --border-radius: 8px;
    --speed: 500ms;
  }

  .coffee-img{
    position: relative;
    height: 100px;
    width: 100px;
    overflow: hidden;
    background-color:--primary-text-color;
  }
  
   .enter{
    // position: absolute;
    // top: 20px;
    // right: 20px;
    height: 4.5vh;
    width: 7vw;
    border-radius: 10px;
    overflow: hidden;
    cursor: pointer;
    font-size: 1em;
    font-weight:500;
    background-color:$accent-variant;
    transition:  all 0.3s;
                &:hover{
                    transform: scale(1.05);
                }
  }

  @media handheld, only screen and (max-width: 767px) {
    .enter{
      // position: absolute;
      // top: 20px;
      // right: 20px;
      height: 3.5vh;
      width: 15vw;
      border-radius: 10px;
      overflow: hidden;
      cursor: pointer;
      font-size: 0.75em;
      font-weight:500;
      background-color:$accent-variant;
      transition:  all 0.3s;
                  &:hover{
                      transform: scale(1.05);
                  }
    }
}

  .welcome{
    width: 100%;
    text-align: center;
    font-size: 18px;
    // padding: 20px 0;
    font-weight: 500;
    color:$accent-variant;
    // line-height: 1.2rem;
  }
  .correo{
    text-align: center;
    font-size: 15px;
    margin-bottom: 1px;
    margin-top: 5px;
  }
  .formcorreo{
    display: flex;
    align-items: center;
    flex-direction: column;
    input{
    margin-bottom: 20px ;
    border-color:$accent;
    width: 20vw;
    border: 0;
    height:4.5vh;
    border-radius: 10px ;
    font-size: 1em;
    text-align: center;
    transition: all 0.3s;

    }
  }
  .dropdown-menu-sign {
    position: absolute;
    top: 10vh;
    right: 20px;
    border-radius: var(--border-radius);
    padding: 0px 20px;
    width: 20vw;
  }
  /* Cuadrado Girado 45Â° */
  .dropdown-menu-sign::before{
    content: '';
    position: absolute;
    top: -5px;
    right: 20px;
    height: 20px;
    width: 20px;
    background: var(--primary-text-color);
    transform: rotate(45deg);
  }
  
  .dropdown-menu-sign.active{
    background-color: var(--primary-text-color);
    opacity: 0.95;
    visibility: visible;
    transform: translateY(0);
    transition: var(--speed) ease;
    z-index: 2;
    text-align: center;
    padding-bottom: 4.5vh;

    a {
    text-decoration: none;  
    color:$accent-variant;     
    }
    button {
      margin-top: 2.5vh;
      height: 4.5vh;
      width: 20vw;
      border-radius: 10px;
      overflow: hidden;
      cursor: pointer;
      font-size: 1em;
      font-weight:500;
      background-color:$accent-variant;
      transition:  all 0.3s;
                  &:hover{
                      transform: scale(1.05);
                  }
    }
  }

  @media handheld, only screen and (max-width: 767px) {
    .dropdown-menu-sign {
      right: 2vw;
      width: 70vw;
    }

    .formcorreo{
      input{
      width: 50vw;
      border: 0;
      height:4.5vh;
      border-radius: 10px ;
      font-size: 1em;
      text-align: center;
      transition: all 0.3s;
  
      }
    }
    
  }
  
  .dropdown-menu-sign.inactive{
    opacity: 0;
    visibility: hidden;
    transform: translateY(-20px);
    transition: var(--speed) ease;
  }
  


  
 
  